<template>
  <div class="notes">
            <div class="note" :class="{full: !grid}" v-for="(note, index) in notes" :key="index">
                <div class="note-header">
                    <p :class="note.priority" @click="note.edition = true" v-show="!note.edition"> {{ note.title }} </p>
                    <input type="text" @keyup.enter="note.edition = false" v-show="note.edition" v-model="note.title">
                    <p style="cursor: pointer;" v-show="!note.edition" @click="removeNote(index)">x</p>
                </div>
                <div class="note-body">
                    <p>{{ note.desc }}</p>
                    <span>{{ note.date }}</span>
                </div>
            </div>
        </div>
</template>

<script>
export default {
    props: {
        notes: {
            type: Array,
            required: true
        },
        grid: {
            type: Boolean,
            required: true
        },
    },
    methods: {
        removeNote (index) {
            this.$emit('remove', index)
        }
    }
}
</script>

<style lang="scss">
    .notes {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .note {
        width: 46%;
        padding: 18px 20px;
        margin-bottom: 20px;
        background-color: #fff;
        transition: all .25s cubic-bezier(.02,.01,.47,1);
        box-shadow: 0 30px 30px rgba(0, 0, 0, .02);
        &.full{
            width: 100%;
            text-align: center;
            .note-header {
                justify-content: center;
                p {
                    margin-right: 15px;
                    &:last-child {
                        margin-right: 0;
                    }
                }
        
            }
        }
        &:hover {
            box-shadow: 0 30px 30px rgba(0, 0, 0, .04);
            transform: translate(0,-6px);
            transition-delay: 0s !important;
            
        }
    }
    .note-header {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 3%;
        h1 {
            font-size: 32px;
        }
        p {
            color: #402caf;
            font-size: 22px;
        }
        svg {
            margin-right: 12px;
            color: #999;
            cursor: pointer;
            &:last-child {
                margin-right: 0
            }
            &.active {
                color: #402caf;
            }
        }

        .important {
            color: yellow;
        }

        .very-important {
            color: red;
        }
    }
    .note-body{
        p {
            margin: 20px 0
        }
        span {
            font-size: 14px;
            color: #999;
        }
    }
</style>