<template>
<div class="new-note">
    <label class="subtitle">Title</label>
    <div class="note-title">
            <input class="title-input" v-model="note.title" type="text">
    <form class="title-form">
        <label class="radio"><input v-model="note.priority" type="radio" value="standart"><span>Standart</span></label>
        <label class="radio"><input v-model="note.priority" type="radio" value="important"><span>Important</span></label>
        <label class="radio"><input v-model="note.priority" type="radio" value="very-important"><span>Very important</span></label>
  </form>
    </div>
    <label class="subtitle">Description</label>
            <textarea v-model="note.desc"></textarea>
            <button class="btn btnPrimary" @click="addNote">New note</button>
        </div>
</template>

<script>
export default {
    props: {
        note: {
            type: Object,
            required: true
        },
    },
    methods: {
        addNote() {
            this.$emit('addNote', this.note)
     }
    }
}
</script>
<style lang="scss">

.new-note {
    text-align: center;
}
.btn {
    margin: 3% 0;
}

.note-title{
    display: block;
}

.title-input {
    width: 100%;
}

.title-form {
    display: flex;
    justify-content: space-around;
    margin-bottom: 30px;
    padding-left: 30px;

}

$radioSize: 22px;
$radioBorder: #D1D7E3;
$radioActive: #5D9BFB;

.radio {
    display: block;
    cursor: pointer;
    input {
        display: none;
        & + span {
            line-height: $radioSize;
            height: $radioSize;
            padding-left: $radioSize;
            display: block;
            position: relative;
            &:not(:empty) {
                padding-left: $radioSize + 8;
            }
            &:before,
            &:after {
                content: '';
                width: $radioSize;
                height: $radioSize;
                display: block;
                border-radius: 50%;
                left: 0;
                top: 0;
                position: absolute;
            }
            &:before {
                background: $radioBorder;
                transition: background .2s ease, transform .4s cubic-bezier(.175, .885, .32, 2);
            }
            &:after {
                background: #fff;
                transform: scale(.78);
                transition: transform .6s cubic-bezier(.175, .885, .32, 1.4);
            }
        }
        &:checked + span {
            &:before {
                transform: scale(1.04);
                background: $radioActive;
            }
            &:after {
                transform: scale(.4);
                transition: transform .3s ease;
            }
        }
    }
    &:hover {
        input {
            & + span {
                &:before {
                    transform: scale(.92);
                }
                &:after {
                    transform: scale(.74);
                }
            }
            &:checked + span {
                &:after {
                    transform: scale(.4);
                }
            }
        }
    }
}
</style>